<template>
	<view class="content">
		
		<!-- 导航栏 开始 -->
		<uni-nav-bar background-color="#d81e06" color="#fff" :status-bar="true" @clickLeft="leftNavTap">
		    <view>index</view>
		    <view slot="left"> 
				<text class="iconfont icon-houtui"></text> 
			</view>
		</uni-nav-bar>
		<!-- 导航栏 结束 -->
		
		<!-- 轮播图 开始 -->
		<view>
			<swiper class="swiper" 
					:autoplay="autoplay" 
				    :interval="interval"
				    :duration="duration" 
				    :indicator-dots="indicatorDots">
				<swiper-item 
				class="swiper-item" 
				v-for="(item, index) in swiperList">
				 <image :src="item.image_src" mode="widthFix"></image>
				 </swiper-item>
			</swiper>
		</view>
		<!-- 轮播图 结束 -->
		
		<!-- 图片 -->
		<image class="logo" :src="imageUrl"></image>
		<!-- 图标 -->
		<text class="iconfont icon-shoucang"></text>
		<!-- 循环 -->
		<view v-for="(item, index) in list" @click="listChange(item.name)" :key="item.name">
			名字： {{item.name}}---年龄： {{item.age}}
		</view>
		<!-- v-if -->
		<view v-if="currentName">{{currentName}}</view>
		
		<button @click="openPopupBase">基础弹窗</button>
		<uni-popup ref="popupBase" type="center"><view class="popup-content">基础弹窗内容</view></uni-popup>
		
	</view>
</template>

<script>
	import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
	import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
	export default {
		//引入组件
		components: {
			uniNavBar,
			uniPopup,
			uniPopupMessage,
			uniPopupDialog
		},
		//数据
		data() {
			return {
				title: 'Hello',
				imageUrl: '/static/assets/images/logo.png',
				list: [{
						name: '刘能',
						age: 29
					},
					{
						name: '赵四',
						age: 39
					},
					{
						name: '宋小宝',
						age: 49
					},
					{
						name: '小沈阳',
						age: 59
					}
				],
				currentName: null,
				/*********** 轮播图 *******************/
				swiperList: [],
				background: ['color1', 'color2', 'color3'],
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				duration: 500
			}
		},
		//接收传值的生命周期
		onLoad(e) {
			let that = this;
			console.log(e);
			that.$store.dispatch('getUserInfo', {
				name: '小王'
			}).then(res => {
			
			});
			that.getSwiperList();
		},
		//方法
		methods: {
			//列表切换选择
			listChange(name) {
				this.currentName = name;
			},
			//轮播图接口
			getSwiperList(){
				let that = this;
				that.$request('/home/swiperdata', {}, 'GET').then(res => {
					console.log(res);
					that.swiperList = res.data.message;
				}).catch(error => {
					console.log(error);
				})
				
			},
			//自定义导航 - 左侧点击
			leftNavTap(){
				console.log(1);
			},
			//打开弹窗
			openPopupBase(){
				this.$refs.popupBase.open();
			}
		}
	}
</script>

<style lang="less">
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
	.swiper{
		width:750rpx;
		height:340rpx;
		background-color: #eee;
		.swiper-item{
			width:100%;
			image{
				width:100%;
			}
		}
	}
	.popup-content{
	
	}
</style>
